import React, {useEffect} from 'react'
import PropTypes from 'prop-types'
import { connect  } from 'react-redux'
import {useHistory} from "react-router-dom";
import {Alert, Button, Col, Form, Input,Steps, Row} from "antd";
import {LeftOutlined, LockOutlined,LoadingOutlined,CheckCircleOutlined, MailOutlined} from "@ant-design/icons";
import {motion} from "framer-motion";
import ApiService from "../../../services/Api.service";
import RegisterVillaStep from "./register-villa-step";
const rules = {
    email: [
        {
            required: true,
            message: 'Please input your email address'
        },
        {
            type: 'email',
            message: 'Please enter a validate email!'
        }
    ],
    password: [
        {
            required: true,
            message: 'Please input your password'
        }
    ],
    confirm: [
        {
            required: true,
            message: 'Please confirm your password!'
        },
        ({ getFieldValue }) => ({
            validator(rule, value) {
                if (!value || getFieldValue(['user','password']) === value) {
                    return Promise.resolve();
                }
                return Promise.reject('Passwords do not match!');
            },
        })
    ]
}

const StepAccount = (props)=> {
    let { step, setStep,form  } = props
    const onNext = ()=> {
        form.validateFields()
            .then(()=> {
                setStep(step + 1)
            })
    }
    return (
        <div style={{display:step === 1 ? "block":"none"}}>
            <Row gutter={24}>
                <Col xs={24} lg={12}>
                    <Form.Item
                        name={['user','first_name']}
                        label="First Name"
                        rules={[{required:true,message:"first_name cannot be empty!"}]}
                        hasFeedback
                    >
                        <Input prefix={<MailOutlined className="text-primary" />}/>
                    </Form.Item>

                </Col>
                <Col xs={24} lg={12}>
                    <Form.Item
                        name={['user','last_name']}
                        label="Last Name"
                        rules={[{required:true,message:"last_name cannot be empty!"}]}
                        hasFeedback
                    >
                        <Input prefix={<MailOutlined className="text-primary" />}/>
                    </Form.Item>
                </Col>
            </Row>

            <Form.Item
                name={['user','email']}
                label="Email"
                rules={rules.email}
                hasFeedback
            >
                <Input prefix={<MailOutlined className="text-primary" />}/>
            </Form.Item>
            <Form.Item
                name={['user','password']}
                label="Password"
                rules={rules.password}
                hasFeedback
            >
                <Input.Password prefix={<LockOutlined className="text-primary" />}/>
            </Form.Item>
            <Form.Item
                name={['user','confirm']}
                label="ConfirmPassword"
                rules={rules.confirm}
                hasFeedback
            >
                <Input.Password prefix={<LockOutlined className="text-primary" />}/>
            </Form.Item>
            <Button type={'primary'} className={'w-full'} onClick={onNext}>Next</Button>
        </div>
    )
}

const RegisterVilla = (props)=> {
    const { showLoading, token, loading, redirect, message, showMessage, hideAuthMessage, authenticated, allowRedirect } = props
    const [form] = Form.useForm();
    let history = useHistory();

    const [step,setStep]= React.useState(1)
    const onSignUp = () => {
        form.validateFields().then(values => {
            showLoading()
            const formdata = new URLSearchParams()
            Object.keys(values).forEach((key)=> {
                formdata.append(key,values[key])
            })
            new ApiService({
                url:"/api/v1/auth/signup",
                body:formdata,
            }).post()
                .then(()=> {
                    hideAuthMessage();
                })
        }).catch(info => {
            hideAuthMessage();
        });
    }
    return (
        <div className="flex flex-col h-[100vh] overflow-y-auto py-10  relative">
            <div className="w-full relative pb-10 lg:pb-0">
                <Row justify="center">
                    <Col xs={24} sm={24} md={20} lg={18}>
                        <Button type={'link'} icon={<LeftOutlined />} className={'p-0 m-0'} onClick={()=>history.goBack()} size={'small'}>Back</Button>
                        <h1>{step === 1 ? "Sign Up" : step === 2 ? "Villa Profile":"-"}</h1>

                        <div className="w-full flex items-center lg:flex">
                            <Steps current={step - 1} size={'small'} direction={'horizontal'}>
                                <Steps.Step
                                    icon={step  === 1? <LoadingOutlined /> : step > 1 ? <CheckCircleOutlined /> : null}
                                    title={step > 1 ? "Account Finished" : "Account"}/>
                                <Steps.Step title="Villa"/>
                            </Steps>
                        </div>
                    </Col>
                </Row>

                <div className="overflow-x-hidden  lg:py-10">
                    <Row justify="center">
                        <Col xs={24} sm={24} md={20} lg={18} xl={18}>
                            <div className={'space-y-6'}>
                                <>
                                    <motion.div
                                        initial={{ opacity: 0, marginBottom: 0 }}
                                        animate={{
                                            opacity: showMessage ? 1 : 0,
                                            marginBottom: showMessage ? 20 : 0
                                        }}>
                                        <Alert type="error" showIcon message={message}></Alert>
                                    </motion.div>

                                    <Form
                                        form={form}
                                        layout="vertical"
                                        name="register-form"
                                        onFinish={onSignUp}>
                                        <div className={''}>
                                            <StepAccount
                                                form={form}
                                                step={step}
                                                setStep={setStep}
                                            />

                                            <RegisterVillaStep
                                                form={form}
                                                step={step}
                                                setStep={setStep}
                                            />
                                        </div>
                                        {/*<Form.Item>*/}
                                        {/*    <Button type="primary" htmlType="submit" block loading={loading}>*/}
                                        {/*        Sign Up*/}
                                        {/*    </Button>*/}
                                        {/*</Form.Item>*/}
                                    </Form>
                                </>
                                <p className={'pt-6'}>Already have an account? <a href="/auth/login">Sign In</a></p>
                            </div>
                        </Col>

                    </Row>

                </div>

            </div>
        </div>
    )
}


RegisterVilla.propTypes = {}
RegisterVilla.defaultProps = {}

export default connect(()=> {
    return {}
},{})(React.memo(RegisterVilla))