import React, {useState} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {Button, Card, Col, Form, Input, message, notification, Row, Upload} from "antd";
import {LoadingOutlined, MailOutlined} from "@ant-design/icons";
import CustomIcon from "../../../components/util-components/CustomIcon";
import {ImageSvg} from "../../../assets/svg/icon";
import Utils from "../../../utils";
import ApiService from "../../../services/Api.service";
import LocationField from "../../my-views/villa/villa-profile/LocationField";
import FacilityField from "../../my-views/villa/villa-profile/FacilityField";
import ProfileField from "../../my-views/villa/villa-profile/ProfileField";
import SocialField from "../../my-views/villa/villa-profile/SocialField";



const {Dragger} = Upload;


const RegisterVillaStep = (props) => {
    let {step, setStep, form} = props
    const onNext = () => {
        form.validateFields()
            .then((value) => {
            })
    }
    const onBack = () => {
        setStep(step - 1)
    }


    const [progressed, setProgressed] = useState(0)
    const [fileList,setFileList] = useState({})
    const [ files,setFiles] = useState([])
    const [imageUrl, setImageUrl] = useState(() => {
        return null
    })
    const [loading, setLoading] = useState(false)
    const beforeUpload = file => {
        const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/jpg';
        if (!isJpgOrPng) {
            message.error('You can only upload JPG/PNG file!');
        }
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
            message.error('Image must smaller than 2MB!');
        }
        return isJpgOrPng && isLt2M;
    }
    const onUploadChange = async (info) => {
        const {onSuccess, onError, file, onProgress} = info;
        setFiles([file])
        setFileList(file)

        const formdata = new FormData()
        formdata.append(`files`, file)

        await Utils.getBase64(file, imgUrl => {
            setImageUrl(imgUrl)
        });
        onSuccess('OK')
        // await new ApiService({
        //     url: `/api/v1/villa/self/update/thumbnail`,
        //     body: formdata,
        //     config: {
        //         headers: {
        //             "Authorization": `Bearer ${Utils.getToken('get')}`,
        //             "Content-Type": "application/formdata"
        //         },
        //         onUploadProgress: event => {
        //             let progres = (event.loaded / event.total) * 100
        //             setProgressed(progres)
        //             onProgress({percent: (event.loaded / event.total) * 100}, file);
        //             if(progres === 100){
        //
        //             }
        //
        //         }
        //     }
        // })
        //     .put()
        //     .then(({data}) => {
        //         onSuccess(file);
        //
        //         notification.success({
        //             message: 'Success save to database',
        //             description: 'the data has been successfully updated',
        //             duration: 2
        //         });
        //         // setLoading(false)
        //     })
        //     .catch((err) => {
        //         const error = new Error('Some error');
        //         onError({event: error});
        //         message.error({
        //             content: err?.message ??  "Successfully!",
        //             key: "updatable",
        //             duration: 2
        //         })
        //     })
    }


    const onFinish = async ()=> {
        form.validateFields()
            .then(async (value)=> {
                let user = {}
                message.loading({content:"Loading...",key:"updatable"})
                const formdata = new URLSearchParams()
                if(typeof(value?.user) !== "undefined"){
                    Reflect.deleteProperty(value?.user,'confirm')
                    Object.keys(value.user).forEach(key => {
                        if(key === "email"){
                            formdata.append('username',value?.user?.email?.split("@")[0])
                            formdata.append('email',value.user[key])
                        }else if(key === 'first_name'){
                            formdata.append('firstName',value.user[key])
                        }
                        else if(key === 'last_name'){
                            formdata.append('lastName',value.user[key])
                        } else{
                            formdata.append(key,value.user[key])
                        }

                    })
                    formdata.append('role','villa')
                    Reflect.deleteProperty(value,'user')
                }
                console.log(files)
                await new ApiService({
                    url:"/api/v1/auth/signup",
                    body:formdata
                }).post()
                    .then(async (result)=> {
                        message.loading({content:"Create Villa Profile Loading...",key:"updatable"})
                        let token = result?.data?.token
                        await new ApiService({
                            url:"/api/v1/villa/self/profile/update",
                            body:value,
                            config: {
                                headers: {
                                    Authorization: `Bearer ${token}`
                                }
                            }
                        }).put()
                            .then(async (response)=> {
                                message.success({content:"Success Update Villa Profile!",key:"updatable"})

                                message.loading({content:"Uploaded Image Villa Loading...",key:"updatable"})

                                console.log(files)
                                if(Array.isArray(files) && files.length > 0) {
                                    const formData = new FormData()

                                    formData.append('files', files[0])
                                    await new ApiService({
                                        url: `/api/v1/villa/self/update/thumbnail`,
                                        body: formData,
                                        config: {
                                            headers: {
                                                "Authorization": `Bearer ${token}`,
                                                "Content-Type": "multipart/form-data"
                                            },
                                            onUploadProgress: event => {
                                                let progres = (event.loaded / event.total) * 100
                                                setProgressed(progres)
                                            }
                                        }
                                    })
                                        .put()
                                        .then(({data}) => {
                                            notification.success({
                                                message: 'Success save to database',
                                                description: 'the data has been successfully updated',
                                                duration: 2
                                            });
                                            // setLoading(false)
                                        })
                                        .catch((err) => {
                                            const error = new Error('Some error');
                                            message.error({
                                                content: err?.message ?? "Successfully!",
                                                key: "updatable",
                                                duration: 2
                                            })
                                        })
                                }
                            })
                            .catch((err)=> {
                                console.log({err},'CREATE VILLA')
                            })

                        Utils.getToken('set',token)

                    })
                    .catch((err)=> {
                        console.log({err})
                    })
            })
            .catch((err)=> {
                console.log({err})
            })
    }

    return step === 2 && (
        <React.Fragment>
            <Row gutter={24}>
                <Col xs={{
                    span: 24,
                    order: 1
                }}
                     md={{
                         span: 8,
                         order: 1,
                     }}>
                    <Row gutter={24}>
                        <Col xs={{
                            span: 24,
                            order: 1
                        }}
                             lg={{
                                 span: 24,
                                 order: 1,
                             }}
                        >
                            <Dragger
                                accept="image/*"
                                multiple={false}
                                // listType="card"
                                maxCount={1}
                                onRemove={false}
                                fileList={[]}
                                customRequest={onUploadChange}
                                // className={'image-upload-grid'}
                                beforeUpload={beforeUpload}
                            >
                                {
                                    imageUrl ?
                                        <img src={imageUrl} alt="avatar"
                                             className="absolute top-0 left-0 rounded-xl w-full h-full object-cover"/>
                                        :
                                        <div>
                                            {
                                                loading ?
                                                    <div>
                                                        <LoadingOutlined className="font-size-xxl text-primary"/>
                                                        <div className="mt-3">Uploading</div>
                                                    </div>
                                                    :
                                                    <div>
                                                        <CustomIcon className="display-3" svg={ImageSvg}/>
                                                        <p>Click or drag file to upload</p>
                                                    </div>
                                            }
                                        </div>
                                }
                            </Dragger>
                        </Col>
                        <Col xs={{
                            span: 24,
                            order: 3
                        }}
                             lg={{
                                 span: 24,
                                 order: 1,
                             }}>
                            <div className="mt-4">
                                <LocationField/>
                            </div>
                        </Col>


                    </Row>
                </Col>

                <Col
                    xs={{
                        span: 24,
                        order: 2,
                    }}
                    lg={{
                        span:16,
                        order:2,
                    }}
                >
                    <ProfileField withCard={false}/>
                    <FacilityField/>

                    <SocialField/>
                </Col>

            </Row>

            <Row gutter={24}>
                <Col xs={12}>
                    <Button type={'primary'} ghost className={'w-full'} onClick={onBack}>Back</Button>
                </Col>
                <Col xs={12}>
                    <Button type={'primary'} className={'w-full'} onClick={onFinish}>Submit</Button>
                </Col>

            </Row>
        </React.Fragment>
    )
}

RegisterVillaStep.propTypes = {}
RegisterVillaStep.defaultProps = {}

export default connect(() => {
    return {}
}, {})(React.memo(RegisterVillaStep))