import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { LockOutlined, MailOutlined } from '@ant-design/icons';
import {Button, Form, Input, Alert, Row, Col, notification} from "antd";
import { showAuthMessage, showLoading, hideAuthMessage, authenticated } from 'redux/actions/Auth';
import { useHistory } from "react-router-dom";
import { motion } from "framer-motion"
import { first } from 'lodash'
import JwtAuthService from 'services/JwtAuthService'
import ApiService from "../../../services/Api.service";
import Utils from "../../../utils";

const rules = {
	email: [
		{ 
			required: true,
			message: 'Please input your email address'
		},
		{ 
			type: 'email',
			message: 'Please enter a validate email!'
		}
	],
	password: [
		{ 
			required: true,
			message: 'Please input your password'
		}
	],
	confirm: [
		{ 
			required: true,
			message: 'Please confirm your password!'
		},
		({ getFieldValue }) => ({
			validator(rule, value) {
				if (!value || getFieldValue('password') === value) {
					return Promise.resolve();
				}
				return Promise.reject('Passwords do not match!');
			},
		})
	]
}

export const RegisterForm = (props) => {

	const { showLoading, token, loading, redirect, message, showMessage, hideAuthMessage, authenticated, allowRedirect } = props
	const [form] = Form.useForm();
	let history = useHistory();

	const onSignUp = () => {
    	form.validateFields().then(values => {
			showLoading()
			const formdata = new URLSearchParams()
			delete values?.confirm

			Object.keys(values).forEach((key)=> {
				formdata.append(key,values[key])
			})
			formdata.append('username',first(values?.email?.split('@')))
			formdata.append('role','customer')

			new ApiService({
				url:"/api/v1/auth/signup",
				body:formdata
			}).post()
				.then((response)=> {
					console.log({response})
					if(!response?.error){
						notification.success({
							message: response?.message ?? "Register Successfully",
							description: 'Please check your email to verify your account.',
							duration: 0
						});
						// history.push({
						// 	pathname:"/auth/login"
						// })
					}else{
						notification.error({
							message: response?.message ?? "Register Error",
							// description: 'Please check your email to verify your account.',
							duration: 0
						});
					}
					hideAuthMessage();
				})
				.catch((err)=> {
					notification.error({
						message: "Register Error",
						description:err?.message ?? "Some Error",
						duration: 0
					});
					hideAuthMessage();
				})
			hideAuthMessage();
		}).catch(info => {
			notification.error({
				message: "Register Error",
				description: info?.message ?? "Some Error",
				duration: 0
			});
			hideAuthMessage();
		});
	}

	useEffect(() => {
		if(showMessage) {
				setTimeout(() => {
				hideAuthMessage();
			}, 3000);
		}
  });
	
	return (
		<>
			<motion.div 
				initial={{ opacity: 0, marginBottom: 0 }} 
				animate={{ 
					opacity: showMessage ? 1 : 0,
					marginBottom: showMessage ? 20 : 0 
				}}> 
				<Alert type="error" showIcon message={message}></Alert>
			</motion.div>
			<Form
				form={form}
				layout="vertical"
				name="register-form">
				<Row gutter={24}>
					<Col xs={24} lg={12}>
						<Form.Item
							name="firstName"
							label="First Name"
							rules={[{required:true,message: "First Name cannot be empty!"}]}
							hasFeedback
						>
							<Input/>
						</Form.Item>
					</Col>
					<Col xs={24} lg={12}>
						<Form.Item
							name="lastName"
							label="Last Name"
							rules={[{required:true,message: "Last Name cannot be empty!"}]}
							hasFeedback
						>
							<Input/>
						</Form.Item>
					</Col>

				</Row>
				<Form.Item 
					name="email" 
					label="Email" 
					rules={rules.email}
					hasFeedback
				>
					<Input prefix={<MailOutlined className="text-primary" />}/>
				</Form.Item>
				<Form.Item 
					name="password" 
					label="Password" 
					rules={rules.password}
					hasFeedback
				>
					<Input.Password prefix={<LockOutlined className="text-primary" />}/>
				</Form.Item>
				<Form.Item 
					name="confirm" 
					label="ConfirmPassword" 
					rules={rules.confirm}
					hasFeedback
				>
					<Input.Password prefix={<LockOutlined className="text-primary" />}/>
				</Form.Item>
				<Form.Item>
					<Button type="primary" onClick={onSignUp} block loading={loading}>
						Sign Up
					</Button>
				</Form.Item>
			</Form>
		</>
	)
}

const mapStateToProps = ({auth}) => {
	const { loading, message, showMessage, token, redirect } = auth;
  return { loading, message, showMessage, token, redirect }
}

const mapDispatchToProps = {
	showAuthMessage,
	hideAuthMessage,
	showLoading,
	authenticated
}

export default connect(mapStateToProps, mapDispatchToProps)(RegisterForm)
